.contenedor{
    width: 200px;
    overflow: hidden;
    border-radius: 6px;
    margin: 0 auto;
}

.contenedor>img{
    width: 200px;
    position: relative;
    z-index: -1;
}

.contenedor>.capa{
    position: absolute;
    z-index: 50;
    border-radius: 6px;
    top: 0;
    width: 200px;
    height: 267px;
    background: rgba(100, 255, 218, 0.25);
    transition: display 0.5 ease-out;
}

.contenedor>.capa:hover{
    display: none;
}

h2{
